<template>
      <v-sheet
        rounded="lg"
        min-height="85vh"
        class="w-primary"
      >
        <v-tabs v-model="tab" background-color="#eee">
          <v-tab>Təlimatlar</v-tab>
          <v-tab>Video Təlimatlar</v-tab>
          <v-tabs-items v-model="tab" class="w-primary">
          <v-tab-item>
              <v-container>
                  <v-row>
                    <v-col cols="4" v-for="item in instructions" :key="item.GUID">
                      <v-lazy
                        :options="{ threshold: .2 }"
                        transition="fab-transition"
                      >
                        <v-card class="w-secondary">
                          <v-card-title class="w-text">
                            {{item.NAME}}
                            <v-spacer></v-spacer>
                             <v-btn @click="openFullScreenDialog(item.DATAID, item.NAME)" icon><v-icon>mdi-eye</v-icon></v-btn>        
                          </v-card-title>
                        </v-card>
                      </v-lazy>
                    </v-col>
                   <v-dialog
                     v-model="dialog"
                     fullscreen
                     hide-overlay
                     transition="dialog-bottom-transition"
                   >
                     <template v-slot:activator="{ on, attrs }">
                         <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
                     </template>
                      <v-card>
                         <v-toolbar
                           dark
                           color="primary"
                         >
                           <v-btn
                             icon
                             dark
                             @click="dialog = false"
                           >
                             <v-icon>mdi-close</v-icon>
                           </v-btn>
                           <v-toolbar-title>{{title}}</v-toolbar-title>
                         </v-toolbar>  
                         <v-card-text><div v-html="content"></div></v-card-text>                    
                       </v-card>
                   </v-dialog>
                  </v-row>
              </v-container>
          </v-tab-item>
          <v-tab-item>
              <v-container>
                  <v-row>
                    <v-col cols="4" v-for="item in videoInstructions" :key="item.GUID">
                      <v-lazy
                        :options="{ threshold: .2 }"
                        transition="fab-transition"
                      >
                        <v-card class="w-secondary">
                          <v-card-title class="w-text">
                            {{item.NAME}}
                            <v-spacer></v-spacer>
                             <v-btn @click="goToVideo(item.DATAID, item.NAME)" icon><v-icon>mdi-eye</v-icon></v-btn>  
                          </v-card-title>
                        </v-card>
                      </v-lazy>
                    </v-col>
                  </v-row>
              </v-container>
          </v-tab-item>
          </v-tabs-items>
        </v-tabs>
      </v-sheet>
</template>

<script>
import TheInstruction from '../components/TheInstruction.vue';
import { mapActions, mapGetters } from 'vuex';
 export default {
   components: { TheInstruction },
   data: () => ({
      tab: null,
      dialog: false,
      title: '',
      content: '<p>No Available Content!</p>'
    }),
   computed: {
      ...mapGetters('admin', [
            'instructions',
            'videoInstructions'
        ])
    },
   methods: {
      ...mapActions('admin', [
         'getInstructionContent'
      ]),
      openFullScreenDialog: async function(id, title){
        this.content = await this.getInstructionContent(id);
        this.dialog = true;
        this.title = title;
      },
      goToVideo: function(id, title){
        const url = `${opentext.mainurl}/${id}/${title}.mp4?func=doc.Fetch&nodeid=${id}&viewType=1`
        window.open(url, '_blank').focus();
      }
   }
 }
</script>

<style lang="scss" scoped>
  .w-primary{
    background-color: #eee!important;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
  .w-text{
    color: black;
  }
</style>
