<template>
    <div>
      <v-card>
        <v-card-title>CEO Message</v-card-title>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Adı
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr v-if="ceoMessages.length == 0">
                  <td colspan="2">
                    <v-icon>mdi-block-helper</v-icon> No messages yet
                  </td>
                </tr>
                <tr
                  v-for="item in ceoMessages"
                  :key="item.ID"
                >
                  <td>{{ item.HEADLINE }}</td>
                  <td>
                      <v-btn icon>
                          <v-icon>mdi-pencil-outline</v-icon>
                      </v-btn>
                      <v-btn icon @click="deleteMessage(item.ID)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text
            color="secondary"
          >
                  Add
           </v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'TheCeoMessage',
    data: () => ({
      dialog: false,
    }),
    computed: {
      ...mapGetters('admin', [
            'ceoMessages'
        ]),
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions('admin', [
          'deleteCeoMessage'
        ]),
        deleteMessage: async function(id){
          await this.deleteCeoMessage({ID: id});
        },
        close: function(){
            this.dialog = false;
        },
    }
}
</script>

<style lang="scss" scoped>
  .dialog-name{
    font-size: 0.8em;
  }
  .v-error{
    color: red;
  }
</style>