<template>
<v-app id="inspire" class="app-main">
  <v-main class="bg-color">
    <layout></layout>
  </v-main>
</v-app>
</template>

<script>
import LayoutDefault from './layouts/LayoutDefault.vue';
import Layout from './layouts/Layout.vue';

export default {
  name: 'App',
  components:{
    LayoutDefault,
    Layout
  },
  data: () => ({
    appNavColor: 'white',
    mainStyles: 'grey lighten-3'
  }),
  methods:{
    changeTheme: function(){
          this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
          if(this.$vuetify.theme.dark){
            this.appNavColor = 'dark';
            this.mainStyles = '';
          }else{
            this.appNavColor = 'white';
            this.mainStyles = 'grey lighten-3'
          }
    },
    exitApp: function(){
      window.location.href = opentext.mainurl;
    }
  }
};
</script>

<style lang="scss">
  .program-name{
    font-weight: bold;
  }
  #inspire{
      margin-top: 11px;
  }
  .app-main{
     font-family: Tahoma, Verdana, Geneva, Arial, Helvetica, sans-serif !important;
     font-size: 11px !important;
  }
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
</style>
