<template>
    <v-sheet
      rounded="lg"
      class="w-primary"
    >
      <v-container>
        <v-row>
          <v-col cols="2"></v-col>
          <v-col>
            <status></status>
          </v-col>
          <v-col cols="2"></v-col>    
        </v-row>   
        <public-post v-for="post in publicPosts" :key="post.id" :postData="post"></public-post>
      </v-container>  
    </v-sheet>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import PublicPost from '../components/PublicPost.vue';
import Status from "../components/Status.vue";
export default {
  name: 'NewsFeed',
  components: { Status, PublicPost },
  computed: {
    ...mapGetters('newsFeed', [
      'publicPosts'
    ])
  },
  methods: {
    ...mapActions('newsFeed', [
            'loadPosts'
      ]),
  },
  created: function() {
    this.loadPosts();
  }
}
</script>

<style lang="scss" scoped>
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
</style>
