<template>
    <div style="display: flex;">
      <v-sheet color="#eee">
        <v-list color="#eee" class="pos-sticky">
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="home"
                :size="36"
                class="v-hover v-grey"
                @click="setType('Home')"
              >
                <v-icon>mdi-home</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="calendar"
                :size="36"
                class="v-hover"
                @click="setType('Calendar')"
              >
                <v-icon>mdi-calendar</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="chat"
                :size="36"
                class="v-hover"
                @click="setType('Chat')"
              >
                <v-icon>mdi-chat-outline</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="hr"
                :size="36"
                class="v-hover"
                @click="setType('HR')"
              >
                <v-icon>mdi-account-tie</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="ceo"
                :size="36"
                class="v-hover"
                @click="setType('CEO')"
              >
                <v-icon>mdi-account-star</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="settings"
                :size="36"
                class="v-hover"
                @click="setType('Settings')"
              >
                <v-icon>mdi-cog</v-icon>
              </v-avatar>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-sheet>
      <v-container fluid class="fix-container">
        <v-row v-if="!onlyMiddle">
          <v-col
            cols="2"
          >
          <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              color="bg-color"
            >
            <v-row>
                <v-col cols="3">
                    <v-avatar
                      class="mt-2"
                      color="primary"
                      size="40"
                      tile
                    >
                      <span class="initials" v-if="!user.photo">{{initials}}</span>
                      <img :src="user.photo" v-if="user.photo">
                    </v-avatar>
                </v-col>
                <v-col class="mt-2">
                    <h3>Vugar Eyvazov</h3>
                    <p><a href="#" class="edit-link" @click="viewProfile()">View Profile</a></p>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-list
              dense
              class="w-menu"
            >
              <v-list-item-group v-model="selectedItem" color="primary">
                <v-list-item 
                    v-for="(item, i) in items"
                    :key="i"
                    @click="goToPage(item)"
                >
                  <v-list-item-content>
                    <v-list-item-title>{{item}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
            </v-sheet>
          </div>
          </v-col>

          <v-col
            cols="7"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>

          <v-col
            cols="3"
          >
            <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
               <v-card class="w-secondary">
                 <v-card-title>
                  <span> <v-icon>mdi-bullhorn</v-icon> Announcement </span>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Excepturi, quo vitae doloribus eaque neque illo ab libero iste hic veritatis eveniet consequuntur dolore ad, eum odio expedita officiis delectus! Quo.
                  </v-card-text>
              </v-card>
              <v-card class="mt-2 w-secondary">
                <v-card-title><span><v-icon>mdi-account-star</v-icon>CEO</span></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                   Happy Novruz
                </v-card-text>
              </v-card>
              <v-card class="mt-2 w-secondary">
                <v-card-title><span><v-icon>mdi-poll</v-icon> POLL</span></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    No Active Poll
                </v-card-text>
              </v-card>
            </v-sheet>
            </div>
          </v-col>
        </v-row>
        <v-row v-if="onlyMiddle">
          <v-col cols="1"></v-col>
          <v-col cols="10">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>
      </v-container>
    </div>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 export default {
   name: 'LayoutDefault',
   data: () => ({ 
     items: ['News Feed', 'Innovations', 'HR News', 'New Vacancies', 'Instructions', 'Report'],
     selectedItem: 0,
     isClicked: false,
     onlyMiddle: false,
     type: 'Home' 
   }),
   watch: {
     type: function(val){
       switch (val) {
         case 'Home':
           this.onlyMiddle = false;
           this.goTo('/newsFeed');
           break;
         case 'Chat':
           this.onlyMiddle = true;
           this.goTo('chat');
           break;
          case 'Settings':
           this.onlyMiddle = true;
           this.goTo('admin');
            break;
          case 'Calendar':
            this.onlyMiddle = true;
            this.goTo('calendar');
            break;
          case 'CEO':
            this.onlyMiddle = true;
            this.goTo('ceo');
         default:
           break;
       }
     }
   },
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ])
   },
   methods: {
     ...mapActions(['initialLoad']),
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhoto'
     ]),
     ...mapActions('admin', [
       'getPollData'
     ]),
     setType: function(t){
      this.type = t;
      for (const el of document.getElementsByClassName('v-hover')) {
          el.classList.remove('v-grey');
      }
      var idName = t.toLowerCase();
      document.getElementById(idName).classList.add('v-grey');
     }, 
     viewProfile(){
       this.goTo('profile');
     },
     goToPage(item){
       switch (item) {
          case 'News Feed':
           this.goTo('newsFeed');
           break;
          case 'Instructions':
           this.goTo('instructions');
           break;
          case 'HR':
            this.goTo('hr');
            break;
          case 'Report':
            this.goTo('report');
            break;
          case 'CEO':
            this.goTo('ceo');
            break;
         default:
           this.goTo('/')
           break;
       }
     },
     goTo(routeUrl){
         this.$router.push(routeUrl);
     }
   },
   created: async function(){
     await this.getMemberData();
     await this.getMemberPhoto();
     await this.getPollData();
     await this.initialLoad();
     this.goTo('/newsFeed');
   },
 }
</script>

<style lang="scss">
    .fix-container{
      margin-left: 10px;
      margin-right: 10px;
    }
    .bg-color{
        background-color: #f7f8f8 !important;
    }
    .initials {
        color: white
    }
    .edit-link{
        text-decoration: unset;
    }
    .pos-sticky{
      position: sticky;
      top: 10px
    }
    .w-menu{
      background-color: unset !important;;
    }
    #v-nav{
      position: sticky;
    }
    .v-hover:hover{
      background-color: grey;
      cursor: pointer;
    }
    .v-grey{
      background-color: grey !important;;
    }
    .w-secondary{
      background-color: #f7f8f8!important;
    }
</style>