import axios from 'axios';
import FormData from 'form-data';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';
import _ from 'lodash';
import Vue from 'vue';

export default {
    namespaced: true,
    state: () => ({
        ceoMessages: [],
        instructions: [],
        videoInstructions: [],
        polls: [],
        newsChannels: [],
        pollData: [],
        activePollData: [],
        questionsForPoll: [],
        pollResult: [],
        myVoteInActivePoll: false,
        motivationHistories: [],
        generalNews: []
    }),
    getters: {
        ceoMessages: (state) => state.ceoMessages, //filter ceo messages later
        instructions: (state) => state.instructions,
        videoInstructions: (state) => state.videoInstructions,
        polls: (state) => state.polls,
        activePollData: (state) => state.activePollData,
        myVoteInActivePoll: (state) => state.myVoteInActivePoll,
        motivationHistories: (state) => state.motivationHistories,
        generalNews: (state) => state.generalNews
    },
    mutations: {
        addNewVideoInstruction: (state, videoInstruction) => {
            state.videoInstructions.push(videoInstruction);
        },
        deleteInstruction: (state, payload) => {
            if(payload.type == 'Instruction'){
                const index = state.instructions.findIndex(x=>x.ID == payload.id);
                state.instructions.splice(index, 1);
            }else{
                const index = state.videoInstructions.findIndex(x=>x.ID == payload.id);
                state.videoInstructions.splice(index, 1);
            }
        },
        deletePoll: (state, payload) => {
            const index = state.polls.findIndex(x=>x.ID == payload.id);
            state.polls.splice(index, 1);
        },
        setActivePolls: (state) => {
            for (const poll of state.pollData) {
                let expiration = new Date(poll.DATEEXPIRATION);
                let today = new Date();
                if(moment(expiration).isAfter(today)){
                    state.activePollData.push(poll);
                }
            }
        },
        setQuestionsForPollSubmit: (state, payload) => {
            for (let i = 0; i < payload.length; i++) {
                const question = `Question_${i+1}`;
                state.questionsForPoll[question] = payload[i].value+1;
            }
        },
        checkMyVoteInActivePoll: (state) => {
            const index = state.pollResult.findIndex(x=>x.USERID == opentext.userId);
            if(index != -1){
                state.myVoteInActivePoll = true;
            }else{
                state.myVoteInActivePoll = false;
            }
        }
    },
    actions: {
        createInstruction: async (context, name) => {
            const details = {
                'func': 'll',
                'objType': '5574',
                'objAction': 'create2',
                'parentId': opentext.instructionId,
                'MimeType': 'text/html',
                'textfield': '<p>your content goes here</p>',
                'name': name,
                'nextURL':  `${opentext.nextUrl}/wiki/${opentext.instructionId}/`,
                'uponCreate': 'WikiPageEditCMD',
                'secureRequestToken':  opentext.secureToken
            };
            let response = await Vue.prototype.$api.general.post(details);
            if(response.url != opentext.mainurl)
                return response.url;
            else
                return '';
        },
        deleteInstruction: async(context, payload) => {
            try{
                const url =  opentext.mainurl + '/api/v1/nodes/' + payload.id;
                const response = await axios.delete(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                context.commit('deleteInstruction', payload);
                console.log(response);
            }catch(err){
                console.log(err);
                return 'This item is reserved';
            } 
        },
        getInstructionContent: async(context, id) => {
            try{
                const url =  opentext.mainurl + `/api/v1/nodes/${id}/content`;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                return response.data;
            }catch(err){
                console.log(err);
            } 
        },
        createVideoInstruction: async (context, payload) => {
            var myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            const formdata = new FormData();
            formdata.append("type", "144");
            formdata.append("parent_id", opentext.videoFolder);
            formdata.append("name", payload.name);
            formdata.append("file", payload.file);
        
            const requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };
            try{
                const response = await fetch(opentext.mainurl + "/api/v2/nodes", requestOptions);
                if (!response.ok) // or check for response.status
                    throw new Error(response.statusText);
                const data = JSON.parse(await response.text());
                let videoInstruction = {};
                videoInstruction.GUID = uuidv4();
                videoInstruction.ID = data.results.data.properties.id;
                videoInstruction.NAME = data.results.data.properties.name; 
                context.commit('addNewVideoInstruction', videoInstruction);
                return data;
            }catch(error){
                return 'Error';
            }
        },
        createPoll: async (context, payload) => {
            const details = {
                'func': 'll',
                'objType': '218',
                'objAction': 'create2',
                'parentId': opentext.cicCommunityPoll,
                'noApplet': 'false',
                'nextURL':  opentext.nextUrl,
                'secureRequestToken':  opentext.secureToken,
                'name': payload.name,
                'comment': payload.description,
                'CREATE_CatNames': '',
                'CTT_ID': opentext.cicCommunityPoll,
                'CTT_PathSaved': 'POLLs',
                'CTT_Path': 'POLLs'
            };
            let response = await Vue.prototype.$api.general.post(details);
            console.log(response);
            await context.dispatch('getPolls');
        },
        addQuestionsOld: async (context, payload) => {
            let cacheId = await context.dispatch('getCacheIDForPOLL',{objId: payload.id});
            let details = {
                'func': 'll',
                'objId': payload.id,
                'CacheID': cacheId,
                'ObjAction': 'EditPoll2', 
                'QuestionIndex': '',
                'ChoiceIndex': '',
                'ChangePollData': '0',
                'noApplet': 'false',
                'secureRequestToken':  opentext.secureToken,
                'NextUrl':  opentext.nextUrl,   
                'name_objtype': '218',
                'name_parent': opentext.cicCommunityPoll,
                'name_orig': payload.oldName,
                'name': payload.name,              
                'Instruction': payload.instruction,             
                'EffectiveDate': 'D/2021/6/11:0:0:0', //payload.effectiveDate, // D/2021/6/9:0:0:0
                'EffectiveDate_dirtyFlag': '0',
                '_datepicker_EffectiveDate': '06/11/2021', //payload.datePicker_effectiveDate, // 06/09/2021,
                'EffectiveDate_hour': '0',
                'EffectiveDate_minute': '0',
                'EffectiveDate_second': '0',
                'EffectiveDate_ampm': '0',

                'ExpiredDate':  'D/2021/6/12:0:0:0', //payload.expireDate,
                'ExpiredDate_dirtyFlag': '1',
                '_datepicker_ExpiredDate': '06/12/2021', //payload.datePicker_expireDate,
                'ExpiredDate_hour': '0',
                'ExpiredDate_minute': '0',
                'ExpiredDate_second': '0',
                'ExpiredDate_ampm': '0',            
                //'ViewResult': payload.viewResults, //1
                //'AddComment': payload.viewComments, //0
                'IMG_ID': payload.imgId, // 830888
                'IMG_PathSaved': payload.imgPathSaved, //vugar.i.eyvazov Home:Certification-Digital_Badge-Content_Server_16.2.8_Consultant-FINAL-RGB.png
                'IMG_Path': payload.path,
                'IMG_ALT': payload.img_alt
            };
            Object.assign(details, payload.Questions);
            /*payload.Questions = {
                'Question_1': 'Sual 1',
                'Choice_1_1': 'Ans 1',
                'Choice_1_2': 'Ans 2',
                'PollType_1':'Radio Button' / Checkbox,
                'Question_2': 'Sual 2',
                'Choice_2_1': 'Ans 1',
                'Choice_2_2': 'Ans 2',
                'PollType_2':'Radio Button' / Checkbox
            }*/
            let formBody = [];
            for (var property in details) {
              const encodedKey = encodeURIComponent(property);
              const encodedValue = encodeURIComponent(details[property]);
              formBody.push(encodedKey + "=" + encodedValue);
            }
            formBody = formBody.join("&");
            const response = await fetch(opentext.mainurl, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
              },
              body: formBody
            })
            console.log(response);
        },
        addQuestions: async(context, payload) => {
            let cacheId = await context.dispatch('getCacheIDForPOLL',{objId: payload.id});
            var myHeaders = new Headers();
        
            var formdata = new FormData();
            formdata.append("func", "ll");
            formdata.append("objId", payload.id);
            formdata.append("CacheID", cacheId);
            formdata.append("ObjAction", "EditPoll2");
            formdata.append("QuestionIndex", "");
            formdata.append("ChoiceIndex", "");
            formdata.append("ChangePollData", "0");
            formdata.append("noApplet", "false");
            formdata.append("secureRequestToken", opentext.secureToken);
            formdata.append("NextUrl", opentext.nextUrl);
            formdata.append("name_objtype", "218");
            formdata.append("name_parent", "874618");
            formdata.append("name_orig", payload.name);
            formdata.append("name", payload.name);
            formdata.append("Instruction", payload.instruction);
            formdata.append("EffectiveDate", "D/2021/6/11:0:0:0");
            formdata.append("EffectiveDate_dirtyFlag", "0");
            formdata.append("_datepicker_EffectiveDate", "06/11/2021");
            formdata.append("EffectiveDate_hour", "0");
            formdata.append("EffectiveDate_minute", "0");
            formdata.append("EffectiveDate_second", "0");
            formdata.append("EffectiveDate_ampm", "0");
            formdata.append("ExpiredDate", "D/2021/6/12:0:0:0");
            formdata.append("ExpiredDate_dirtyFlag", "0");
            formdata.append("_datepicker_ExpiredDate", "06/12/2021");
            formdata.append("ExpiredDate_hour", "0");
            formdata.append("ExpiredDate_minute", "0");
            formdata.append("ExpiredDate_second", "0");
            formdata.append("ExpiredDate_ampm", "0");
            // formdata.append("Question_1", "q1");
            // formdata.append("Choice_1_1", "c1");
            // formdata.append("Choice_1_2", "c2");
            // formdata.append("PollType_1", "1");
            // formdata.append("Question_2", "q2");
            // formdata.append("Choice_2_1", "c3");
            // formdata.append("Choice_2_2", "c4");
            // formdata.append("PollType_2", "1");

            for (const key in payload.Questions) {
                if (Object.hasOwnProperty.call(payload.Questions, key)) {
                    const element = payload.Questions[key];
                    formdata.append(key, element);
                }
            }

            formdata.append("IMG_ID", "0");
            formdata.append("IMG_PathSaved", "");
            formdata.append("IMG_Path", "");
            formdata.append("IMG_ALT", "");
            
            var requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };
            
            var response = await fetch(opentext.mainurl, requestOptions)
            var result = await response.text();
            console.log(result);
        },
        deletePoll: async (context, payload) => {
            try{
                const url =  opentext.mainurl + '/api/v1/nodes/' + payload.id;
                const response = await axios.delete(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                context.commit('deletePoll', payload);
                console.log(response);
            }catch(err){
                console.log(err);
            } 
        },
        getCacheIDForPOLL: async (context, payload) => {
            var myHeaders = new Headers();
            var formdata = new FormData();
            formdata.append("func", "ll");
            formdata.append("objId", payload.objId);
            formdata.append("CacheID", "0");
            formdata.append("ObjAction", "EPAddQuestion");
            formdata.append("QuestionIndex", "1");
            formdata.append("ChoiceIndex", "");
            formdata.append("ChangePollData", "0");
            formdata.append("noApplet", "false");
            formdata.append("secureRequestToken", opentext.secureToken);
            formdata.append("NextUrl", opentext.nextUrl);
            formdata.append("name_objtype", "218");
            formdata.append("name_parent", opentext.cicCommunityPoll);
            formdata.append("name_orig", "test");
            formdata.append("name", "test");
            formdata.append("Instruction", "asd");
            formdata.append("EffectiveDate", "D/2021/6/10:0:0:0");
            formdata.append("EffectiveDate_dirtyFlag", "0");
            formdata.append("_datepicker_EffectiveDate", "06/10/2021");
            formdata.append("EffectiveDate_hour", "0");
            formdata.append("EffectiveDate_minute", "0");
            formdata.append("EffectiveDate_second", "0");
            formdata.append("EffectiveDate_ampm", "0");
            formdata.append("ExpiredDate", "D/2021/6/12:0:0:0");
            formdata.append("ExpiredDate_dirtyFlag", "0");
            formdata.append("_datepicker_ExpiredDate", "06/12/2021");
            formdata.append("ExpiredDate_hour", "0");
            formdata.append("ExpiredDate_minute", "0");
            formdata.append("ExpiredDate_second", "0");
            formdata.append("ExpiredDate_ampm", "0");
            formdata.append("Question_1", "ASdsss");
            formdata.append("Choice_1_1", "asd");
            formdata.append("PollType_1", "1");
            formdata.append("IMG_ID", "0");
            formdata.append("IMG_PathSaved", "");
            formdata.append("IMG_Path", "");
            formdata.append("IMG_ALT", "");

            var requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };

            var response = await fetch("http://192.168.14.51/otcs/llisapi.dll", requestOptions)
            var result = await response.text();
            var el = document.createElement('html');
            el.innerHTML = result;
            return el.getElementsByTagName('input')[22].value;
        },
        createChannel: async (context, payload) => {
            var myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            var formdata = new FormData();
            formdata.append("type", "207");
            formdata.append("parent_id", opentext.cicChannels);
            formdata.append("name", payload.name);
        
            var requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };
            try{
                var response = await fetch(opentext.mainurl + "/api/v1/nodes", requestOptions);
                if (!response.ok) // or check for response.status
                    throw new Error(response.statusText);
                console.log(response);
            }catch(error){
                return 'Error';
            }
        },
        createNews: async (context, payload) => {
            var myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            var formdata = new FormData();
            formdata.append("type", "208");
            formdata.append("parent_id", payload.channelId);
            formdata.append("name", payload.name);

            
            var requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body: formdata,
                redirect: 'follow'
              };
            try{
                var response = await fetch(opentext.mainurl + "/api/v1/nodes", requestOptions);
                if (!response.ok)
                    throw new Error(response.statusText);
            }catch(error){
                return 'Error';
            }
        },
        getPollData: async (context) => {
            await context.dispatch('getApiData', {
                prefix: 'admin',
                name: 'pollData',
                webreportId: opentext.webreportIds.childrenId,
                params: `&parentId=${opentext.cicCommunityPoll}&subType=${opentext.subTypes.poll}`
            }, { root:true });   
            context.commit('setActivePolls');
            await context.dispatch('getPollResult');
            context.commit('checkMyVoteInActivePoll');
        },
        convertAssocToJSON: async (context, payload) => {
            try{
                const url =  opentext.mainurl + getApis().convertAssocToJson + '&A=' + payload;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                let data = JSON.parse(response.data.data).result;
                data.pop();
                return data;
            }catch(err){
                console.log(err);
            }
        },
        addPollAnswer: async (context, payload) => {
            const details = {
                'func': 'll',
                'objId': context.state.activePollData[0].DATAID,
                'objAction': 'OpenPoll2',
                'nextURL':  opentext.nextUrl
            };
            context.commit('setQuestionsForPollSubmit', payload);
            Object.assign(details, context.state.questionsForPoll);
            await Vue.prototype.$api.general.post(details);
        },
        createMotivationHistory: async (context, payload) => {
            const id = await getWebCacheId('885260');
            const details = {
                        'func': 'll',
                        'objType': '357',
                        'objAction': 'wizard2',
                        'parentId': '-' + opentext.motivationHistories,
                        'nextURL': opentext.nextUrl,
                        "stepID": '1',
                        "cacheID": id,
                        "secureRequestToken": opentext.secureToken,
                        'attDeleteID': '',
                        'attDeleteType': '',
                        'name': payload.name,
                        'comment': '',
                        'imgFolderID': opentext.ImagesFolderOfZBlog,
                        '_journal_blogContent': payload.content,
                        '_journal_blogPublish': 'on',
                        'InheritRequired': 0,
                        'CREATE_Required': 1,
                        'CREATE_Edited': 0,
                        'CREATE_CacheID': 0,
                        'CREATE_CatNames': '',
                        'CTT_ID': '-' + opentext.motivationHistories,
                        'CTT_PathSaved': 'Z',
                        'ATT_ID': 0,
                        'ATT_PathSaved': '',
                        'ATT_Path': '',
                        'URL_NAME': '',
                        'URL_URL': 'http%3A%2F%2F',
                        'nextStepID': '-1'
            };
            await Vue.prototype.$api.general.post(details);
        },
        deleteCeoMessage: async (context, payload) => {
            const details = {
                'func': 'll',
                'objAction': 'delete2',
                'objId': payload.ID,
                'nextURL': opentext.nextUrl,
                'secureRequestToken': opentext.secureToken,
            }
            await Vue.prototype.$api.general.post(details);
        }
    }
}

