import axios from 'axios';
import FormData from 'form-data';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';

export default {
    namespaced: true,
    state: () => ({
        commentData: [],
        publicPostsData: [],
        publicPosts: [],
        likes: []
    }),
    getters: {
        publicPosts: (state) => state.publicPosts
    },
    mutations: {
        removePost: (state, postId) => {
            const index = state.publicPosts.findIndex(x=>x.postId == postId);
            if(index > -1){
                state.publicPosts.splice(index, 1);
            }
        },
        removeComment: (state, payrol) => {
            const postIndex = state.publicPosts.findIndex(x=>x.postId == payrol.postId);
            const index = state.publicPosts[postIndex].comments.findIndex(x=>x.POSTID == payrol.commentId);
            if(index > -1){
                state.publicPosts[postIndex].comments.splice(index, 1);
            }
        }
    },
    actions: {
        setPublicPosts: async (context) => {
            context.state.publicPosts = [];
            for (const postData of context.state.publicPostsData) {
                let isLike = false;
                if(context.state.likes.find(x=>x.POSTID == postData.POSTID && x.USERID == postData.USERID)){
                    isLike = true;
                }
                const data = JSON.parse(postData.DATA);
                const post = {
                    id: uuidv4(),
                    postId: postData.POSTID,
                    userId: postData.USERID,
                    userName: postData.USERNAME,
                    firstName: postData.FIRSTNAME,
                    lastName: postData.LASTNAME,
                    text: data.text,
                    createdAt: data.created_at,
                    isLike: isLike
                };
                if(data.attachmentID){
                    post.attachmentId = data.attachmentID;
                    const file = await context.dispatch('getFile', post.attachmentId);
                    post.attachment = file;
                }
                post.comments = context.state.commentData.filter(x=>x.CONVERSATIONID == post.postId && x.CONVERSATIONID == JSON.parse(x.DATA).in_reply_to_nid);
                post.internalComments = context.state.commentData.filter(x=>x.CONVERSATIONID == post.postId && x.CONVERSATIONID != JSON.parse(x.DATA).in_reply_to_nid);
                post.likes = context.state.likes.filter(x=>x.POSTID == post.postId);
                context.state.publicPosts.push(post);
            }
        },
        getFile: async (context, fileId) => {
            const url = opentext.mainurl + getApis().getFile + '&idx=' + fileId;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            const file = JSON.parse(response.data.data);
            return file;
        },
        loadPosts: async(context, payload) => {
            await context.dispatch('getApiData', {
                prefix: 'newsFeed',
                name: 'publicPostsData',
                webreportId: opentext.webreportIds.publicPostsId,
                params: payload ? '&date='+moment(payload).format('DD/MM/YYY') : ''
            }, { root:true });
            await context.dispatch('getApiData', {
                prefix: 'newsFeed',
                name: 'commentData',
                webreportId: opentext.webreportIds.allCommentsId
            }, { root: true });
            await context.dispatch('getApiData', {
                prefix: 'newsFeed',
                name: 'likes',
                webreportId: opentext.webreportIds.likesId
            }, { root: true });
            await context.dispatch('setPublicPosts'); 
        },
        createPost: async (context, post) => {
            const formdata = new FormData();
            formdata.append("ATT_ID", "0");
            formdata.append("ATT_PathSaved:", "");
            formdata.append("status", post.postStatus);
            formdata.append("ATT_Path", "");
            formdata.append("post_audience", "1");
            formdata.append("in_comment_on_obj_id", "0");
            formdata.append("eventType", "status");
            formdata.append("in_reply_to_status_id", post.inReplyToStatusId);

            if(post.file){
                formdata.append("AddDesktopDoc", post.file, post.file.name);
            }

            const config = {
              method: 'post',
              url: `${opentext.mainurl}/pulse/statuses/update`,
              data : formdata
            };
            try{
                const response = await axios(config)
                return response.data.status.id;
            }catch(error){
                console.log(error);
            };
        },
        likeOrUnlikePost: async (context, payload) => {
            const url = `${opentext.mainurl}?func=socialobject.LikeOrUnlike&post_id=${payload.postId}&object_id=0&rating_type=${payload.isLike}&feed_type=1`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            console.log(response);
        },
        destroyPost: async (context, postId) => {
            const url = `${opentext.mainurl}/pulse/statuses/destroy/${postId}.json`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            console.log(response);
        }
    }
}