<template>
    <v-row :class="[goRight ? goRight : '', commentRow, commentRowFixMargin ]">
        <v-col cols="3">
            <div class="comment">
                <div><span class="comment-author">{{firstName}} {{lastName}}</span></div>
                <div><span class="comment-post">{{post}}</span></div>
            </div>
            <div>
                <!-- <span class="comment-like"><a :class="{'liked-comment': isCommentLiked}">Like</a></span> -->
            </div>
        </v-col>
        <v-col>
        <v-menu
            absolute
            offset-y
            nudge-left = 80
            nudge-bottom = 18
        >
            <template v-slot:activator="{ on, attrs }">
              <v-btn 
                icon 
                class="float-left edit-btn"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-dots-horizontal</v-icon>
              </v-btn>
            </template>
            <v-list>
                <v-list-item link>
                    <v-list-item-content>
                        <v-list-item-title>Edit</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                 <v-list-item link @click="deletePost">
                    <v-list-item-content>
                        <v-list-item-title>Delete</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-menu>
        </v-col>
    </v-row>
</template>

<script>
import {mapActions, mapMutations} from 'vuex';
export default {
    name: 'TheComment',
    props: ['postId', 'commentObj', 'isCommentLiked', 'goRight'],
    data: () => ({
        commentRow: 'comment-row',
        commentRowFixMargin: 'comment-row-fix-margin',
    }),
    computed: {
        post: function(){
            if(this.commentObj){
                 if(this.commentObj.post){
                    return this.commentObj.post;
                }
                else if(this.commentObj.DATA)
                    return JSON.parse(this.commentObj.DATA).text
            }
        },
        firstName: function(){
            if(this.commentObj){
                return this.commentObj.FIRSTNAME;
            }
        },
        lastName: function(){
            if(this.commentObj){
                return this.commentObj.LASTNAME;
            }
        }
    },
    methods: {
        ...mapActions('newsFeed', [
            'destroyPost',
        ]),
        ...mapMutations('newsFeed', [
            'removeComment'
        ]),
        deletePost: async function() {
            await this.destroyPost(this.commentObj.POSTID);
            this.removeComment({postId: this.postId, commentId: this.commentObj.POSTID})
        },
    }
}
</script>

<style lang="scss" scoped>
    .comment{
        border-radius: 20px;
        background-color: #F0F2F5;
        width: 150px;
        padding: 12px;
        margin-top: 10px;
    }
    .comment-row-fix-margin{
        margin-top: -20px;
    }
    .comment-author{
        font-size: 0.8em;
        font-weight: bold;
    }
    .comment-post{
        font-size: 1em;
    }
    .edit-btn{
        margin-top: 25px;
    }
     .comment-like{
        margin-left: 15px;
    }
    .comment-like a{
        color: black;
    }
    .comment-like a:hover{
        text-decoration: underline;
    }
    .liked-comment{
        color: blue !important ;
    }
    .go-right{
        margin-left: 30px;
    }
</style>