class BaseApiService {
    baseUrl = opentext.mainurl;
  
    constructor() {
    }
  
    getUrl(id = "", params = "") {
      return `${this.baseUrl}/api/v1/nodes/${id}/output?format=json${params}`;
    }
  
    handleErrors(err) {
      // Note: here you may want to add your errors handling
      console.log({ message: "Errors is handled here", err });
    }
}

class ReadOnlyApiService extends BaseApiService {
    constructor(resource) {
      super(resource);
    }
    async get(id, params){
      try{
        let data = await this.getFull(id, params);
        data.pop();
        return data;
      }catch(err){
        this.handleErrors(err);
      }
    }
    async getFull(id, params) {
      try {
        if (!id) throw Error("Id is not provided");
        const config = {
          headers: { 'OTCSTicket': opentext.OTCSTicket }
        }
        const response = await fetch(this.getUrl(id, params), config);
        return JSON.parse((await response.json()).data).result;
      } catch (err) {
        this.handleErrors(err);
      }
    }
}

class ModelApiService extends ReadOnlyApiService {
    constructor(resource) {
      super(resource);
    }
    async post(data) {
      try {
        let formBody = [];
        for (let property in data) {
          const encodedKey = encodeURIComponent(property);
          const encodedValue = encodeURIComponent(data[property]);
          formBody.push(encodedKey + "=" + encodedValue);
        }
        formBody = formBody.join("&");
        const response = await fetch(opentext.mainurl, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          },
          body: formBody
        });
        if (!response.ok) // or check for response.status
              throw new Error(response.statusText);
        return response;
      } catch (err) {
        this.handleErrors(err);
      }
    }
    async put(id, data = {}) {
      if (!id) throw Error("Id is not provided");
      try {
        const response = await fetch(this.getUrl(id), {
          method: "PUT",
          body: JSON.stringify(data)
        });
        const { id: responseId } = response.json();
        return responseId;
      } catch (err) {
        this.handleErrors(err);
      }
    }
    async delete(id) {
      if (!id) throw Error("Id is not provided");
      try {
        await fetch(this.getUrl(id), {
          method: "DELETE"
        });
        return true;
      } catch (err) {
        this.handleErrors(err);
      }
    }
}

export const $api = {
    general: new ModelApiService()
};