import axios from 'axios';
import Vue from 'vue';
export default {
    namespaced: true,
    state: () => ({
        userMessages: []
    }),
    getters: {
        userMessages: (state) => state.userMessages
    },
    mutations: {
    },
    actions: {
       sendMessage: async function(context, payload){
            const formdata = new FormData();
            formdata.append("ATT_ID", "0");
            formdata.append("ATT_PathSaved", "");
            formdata.append("text", payload.msg);
            formdata.append("ATT_Path", "");
            formdata.append("post_audience", "2");
            formdata.append("user", payload.user);

            const config = {
              method: 'post',
              url: `${opentext.mainurl}/pulse/direct_messages/new`,
              data : formdata
            };
            try{
                const response = await axios(config)
                return response.data.direct_message;
            }catch(error){
                console.log(error);
            };
       },
       sendMessagesToMiddleMan: function(context, payload) {
           Vue.prototype.$signalRServerConnection.invoke("SendMessage", payload.from, payload.to, payload.message).catch(function (err) {
                return console.error(err.toString());
            });
       }
    }
}