import axios from 'axios';

export default {
    namespaced: true,
    state: () => ({
        members: []
    }),
    getters: {
        members: (state) => state.members
    },
    mutations: {
    },
    actions: {
        getMembers: async function(context, payload){
            let url = opentext.mainurl + getApis().getMembers;
            url += '&limit='+payload.limit+'&query='+payload.query;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            const data = response.data.data;
            context.state.members = Array.from(data);
        }
    }
}