import axios from 'axios';

export default {
    namespaced: true,
    state: () => ({
        user: {
            userId: '',
            userName: '',
            firstName: '',
            lastName: '',
            middleName: '',
            title: '',
            email: '',
            cellPhone: '',
            officeLocation: '',
            gender: '',
            birthData: '',
            internalPhone: '',
            photo: ''
        },
        userData: {}
    }),
    getters: {
        user: (state) => state.user,
        initials: (state) => state.user.firstName[0] + state.user.lastName[0]
    },
    mutations: {
        setUser: (state, userData) => {
            state.user.userId = userData.data.id;
            state.user.userName = userData.data.name;
            state.user.firstName = userData.data.first_name;
            state.user.lastName = userData.data.last_name;
            state.user.middleName = userData.data.middle_name;
            state.user.title = userData.data.title;
            state.user.email = userData.data.business_email;
            state.user.cellPhone = userData.data.business_phone;
            state.user.internalPhone = userData.data.home_phone;
            state.user.gender = userData.data.gender == 0 ? 'Male' : 'Female';
            state.user.birthData = userData.data.birth_date;
            state.user.officeLocation = userData.data.office_location;
        },
        setImg: (state, blob) => {
            state.user.photo = URL.createObjectURL(blob);
        }
    },
    actions: {
        getMemberData: async (context) => {
            const url = `${opentext.mainurl}/api/v1/members/${opentext.userId}`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
                'Content-Type': 'application/json'
            }});
            context.state.userData = response.data;
            context.commit('setUser', response.data);
        },
        getMemberPhoto: async (context) => {
            if(!context.state.userData.data.photo_url) return;
            
            try{
                const url = `${opentext.mainurl}/api/v1/members/${opentext.userId}/photo`;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }, responseType: 'blob' });
                context.commit('setImg', response.data);
            }catch(err){
                console.log(err);
            }
        },
        addGeneralData: async(context, payload) => {
            const myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            const formdata = new FormData();
            formdata.append("ufunc", "Update");
            formdata.append("func", "user.saveuser");
            formdata.append("userID", payload.userId);
            formdata.append("nextURL", opentext.nextUrl);
            formdata.append("nTab", "LivelinkUsersAndGroupsGeneral");
            formdata.append("secureRequestToken", opentext.secureToken);
            formdata.append("loginName", payload.userName);
            formdata.append("ex_firstName", payload.firstName);
            
            const requestOptions = {
                method: 'POST',
                headers: myHeaders,
                body: formdata
            };

            try{
                const response = await fetch(opentext.mainurl, requestOptions);
                if (!response.ok) // or check for response.status
                    throw new Error(response.statusText);
                console.log(response);
            }catch(error){
                return 'Error';
            }
        }
    }
}