<template>
    <div>
      <v-card>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Təlimatın adı
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in instructions"
                  :key="item.GUID"
                >
                  <td>{{ item.NAME }}</td>
                  <td>
                      <v-btn icon @click="editCurrentInstruction(item.ID)">
                          <v-icon>mdi-pencil-outline</v-icon>
                      </v-btn>
                      <v-btn icon @click="deleteCurrentInstruction(item.ID)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            persistent
            max-width="400px"
            max-height="300px"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text class="float-right" @click="dialog = true">
               <v-icon>mdi-plus</v-icon>Yeni təlimat
              </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <v-container>
                <v-row>
                  <v-col></v-col>
                  <v-col class="d-flex justify-center"><span class="dialog-name">Yeni təlimat</span></v-col>
                  <v-col>
                    <v-btn 
                      icon
                      class="float-right"
                      @click="close"
                     >
                     <v-icon>mdi-close</v-icon>
                    </v-btn> 
                  </v-col>
                </v-row>
              </v-container>           
             </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col>
                      <v-text-field
                        label="Name"
                        required  
                        v-model="instructionName"
                      ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn text @click="createNewInstruction">Yarat</v-btn>
                    <span v-if="showValidation" class="v-error">*Bu adla təlimat mövcuddur</span>
                  </v-col>
                  <v-col v-if="showValidation"></v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
       <v-dialog
        v-model="dialogError"
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            <span>Səhv</span>
            <v-spacer></v-spacer>
          </v-card-title>
          <v-card-text>
            Bu element reserv olunub. Zəhmət olmasa edit edib daha sonra save edin.
          </v-card-text>
          <v-card-actions>
            <v-btn
              color="primary"
              text
              @click="dialogError = false"
            >
              Close
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'Instruction',
    data: () => ({
      valid: true,
      dialog: false,
      instructionName: '',
      showValidation: false,
      dialogError: false
    }),
    computed: {
      ...mapGetters('admin', [
            'instructions'
        ]),
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions('admin', [
            'createInstruction',
            'deleteInstruction'
        ]),
        createNewInstruction: async function(){
          const url = await this.createInstruction(this.instructionName);
          if(url){
            this.dialog = false;
            this.instructionName = '';
            location.href = url;
          }
          else{
            this.showValidation = true;
          }
        },
        deleteCurrentInstruction: async function(id){
          const errorResponse = await this.deleteInstruction({type: 'Instruction', id: id});
          if(errorResponse){
            this.dialogError = true;
          }
        },
        editCurrentInstruction: async function(id){
          location.href = `${opentext.mainurl}?func=ll&objId=${id}&objAction=wikiedit&viewType=1&nexturl=${opentext.nextUrl}`;
        },
        close: function(){
            this.instructionName = '';
            this.showValidation = false;
            this.dialog = false;
        },
    }
}
</script>

<style lang="scss" scoped>
  .dialog-name{
    font-size: 0.8em;
  }
  .v-error{
    color: red;
  }
</style>