<template>
    <div>
        <chat-window
            :current-user-id="currentUserId"
            :rooms="rooms"
            :rooms-loaded="isRoomsLoaded"
            :messages-loaded="isMessagesLoaded"
            :messages="messages"
            :message-actions = "messageActions"
            :show-emojis = "false"
            :show-reaction-emojis = "false"
            @delete-message = "deleteMessageAction"
            @add-room="showRoom"
            @fetch-messages = 'getRoomMessages'
            @send-message = 'sendMsg'
            @typing-message = 'typingMessage'
            height="85vh"
        />
        <v-dialog
            v-model="newRoom"
            max-width="600px"
            @click:outside="close"
        >
            <v-card>
                <v-card-title>
                    Users
                </v-card-title>
                <v-card-text>
                    <v-container v-if="isUsersList">
                        <v-row>
                            <v-col>
                                <v-list flat>
                                     <v-list-item-group>
                                        <v-list-item v-for="(item, i) in members" :key="i" @click="selectUser(item)">
                                            <v-list-item-content>{{item.name_formatted}}</v-list-item-content>
                                        </v-list-item>
                                    </v-list-item-group>
                                </v-list>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-container v-if="!isUsersList">
                        <v-row>
                            <v-col>
                                <v-text-field
                                  label="Limit"
                                  outlined
                                  v-model="limit"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col>
                                <v-text-field
                                  label="Axtar"
                                  outlined
                                  v-model="query"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions class="justify-center">
                    <v-btn text @click="findUser" v-if="!isUsersList">Axtar</v-btn>
                    <v-btn text @click="returnToFindUsers" v-if="isUsersList">Geriyə</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import ChatWindow from 'vue-advanced-chat';
import 'vue-advanced-chat/dist/vue-advanced-chat.css'
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment';
import Vue from 'vue';
export default {
    components: {
        ChatWindow
    },
    computed: {
        ...mapGetters('member', ['members']),
        ...mapGetters('chat', ['userMessages'])
    },
    data() {
        return{
            newRoom: false,
            messageActions: [
              {
                name: 'replyMessage',
                title: 'Reply'
              },
              {
                name: 'deleteMessage',
                title: 'Delete Message',
                onlyMe: true
              }
            ],
            rooms: [],
            isRoomsLoaded: true,
            isMessagesLoaded: false,
            messages: [],
            messagesFromServer: [
                {
                  _id: 7890,
                  content: 'hi',
                  senderId: 1234,
                  username: 'John Doe',
                  avatar: '',
                  date: '13 November 2018',
                  timestamp: '10:20',
                  saved: true,
                  seen: true,
                },
                {
                  _id: 7891,
                  content: ' hi',
                  senderId: 4321,
                  username: 'John Snow',
                  avatar: '',
                  date: '13 November 2019',
                  timestamp: '10:21',
                  saved: true,
                  seen: true,
                  disableReactions: true
                },
                {
                  _id: 7892,
                  content: 'how are you?',
                  senderId: 1234,
                  username: 'John Doe',
                  avatar: '',
                  date: '13 November 2020',
                  timestamp: '10:20',
                  disableReactions: true
                },

            ],
            currentUserId: opentext.userId,
            limit: '',
            query: '',
            isUsersList: false,
            selectedUser: {},
            lastId: null
        }
    },
    created: async function(){
        await  this.getApiData({
            prefix: 'chat',
            name: 'userMessages',
            webreportId: opentext.webreportIds.privateMessagesId,
            params: `&userId=${opentext.userId}`
        });
        this.getRooms();
        Vue.prototype.$signalRServerConnection.on("ReceiveMessage", (from, to, message) => {
            console.log("From: ", from, "To: ", to, message);
            if(from !== opentext.userId)
                this.receiveMsg(message, from, to, 'Vugar', 'Eyvazov')
        });
    },
    methods: {
        ...mapActions([
            'getApiData'
        ]),
        ...mapActions('member', [
            'getMembers'
        ]),
        ...mapActions('chat', [
            'sendMessage',
            'sendMessagesToMiddleMan'
        ]),
        ...mapActions('newsFeed', [
            'destroyPost'
        ]),
        findUser: async function(){
           let payload = {
               limit: this.limit,
               query: this.query
           }
           await this.getMembers(payload);
           this.isUsersList = true;
        },
        showRoom: function(){
             this.newRoom = true;
        },
        returnToFindUsers: function(){
            this.isUsersList = false;
        },
        close: function(){
            this.limit = '';
            this.query = '';
        },
        getRoomMessages: function({room, options}){
            // this.messages = this.messagesFromServer;
            this.messages = [];
            this.isMessagesLoaded = false;
            let userToSpeak = room.users[1];
            let msgs = this.userMessages.filter(x=>x.USERID == userToSpeak._id || x.RECIPIENTID == userToSpeak._id);          let senderFullName = '';
            if(msgs.length == 0) {
                this.isMessagesLoaded = true;
                return;
            }       
            let id = 1;
            for (const msg of msgs) {
                if(msg.USERID == opentext.userId){
                this.isUserId = true;
                senderFullName = opentext.firstName + ' ' + opentext.lastName;
                }
                if(msg.RECIPIENTID ==opentext.userId){
                    this.isUserId = false;
                    senderFullName = msg.RECIPIENTFULLNAME;
                }
                this.messages.push({
                      _id: ++id,
                      nid: msg.NID,
                      content: JSON.parse(msg.EVENTINFO).text,
                      senderId: msg.USERID,
                      username: msg.USERFULLNAME,
                      seen: true,
                      date: moment(new Date()).format('DD MMMM YYYY')
                    })
            }
            this.isMessagesLoaded = true;
        },
        receiveMsg: async function(msg, fromId, toId,  fromFirstName, fromLastName){
            let _id = 1;
            if(this.messages.length > 0){
                let last = this.messages[this.messages.length-1];
                _id = last._id+1;
            }
            let message = {
                  _id: _id,
                  content: msg,
                  senderId: fromId,
                  username: fromFirstName + ' ' + fromLastName,
                  avatar: '',
                  date: moment(new Date()).format('DD MMMM YYYY'),
                  disableReactions: true
            }
            this.messages.push(message);
        },
        sendMsg: async function({ roomId, content, file, replyMessage, usersTag }){
            let _id = 1;
            if(this.messages.length > 0){
                let last = this.messages[this.messages.length-1];
                _id = last._id+1;
            }
            let message = {
                  _id: _id,
                  content: content,
                  senderId: opentext.userId,
                  username: opentext.firstName + ' ' + opentext.lastName,
                  avatar: '',
                  date: moment(new Date()).format('DD MMMM YYYY'),
                  disableReactions: true
            }
            this.lastId = _id; 
            if(file){
                message.file = {
                    name: file.name,
                    size: file.size,
                    type: file.type,
                    extension: file.extension || file.type,
                    url: file.localUrl
                };
                if(file.audio){
                    message.file.audio = true;
                    message.file.duration = file.duration;
                }
            }
                
            if(replyMessage)
                message.replyMessage = {
                    _id: replyMessage._id,
                    content: replyMessage.content,
                    sender_id: replyMessage.senderId
                }
            let room = this.rooms.find(x=>x.roomId == roomId);
            const res = await this.sendMessage({msg: message.content, user: room.users[1]._id});
            console.log(res);
            const res2 = await this.sendMessagesToMiddleMan({from: opentext.userId, to:room.users[1]._id, message: message.content });
            console.log(res2);
            this.messages.push(message);
        },
        typingMessage: function({message, roomId}){
        },
        deleteMessageAction: async function({roomId, message}){
            await this.destroyPost(message.nid);
            this.messages = this.messages.filter(x=>x._id != message._id);
        },
        selectUser: function(user){
            this.selectedUser = user;
            this.addNewRoom({
                id: this.selectedUser.id,
                name: this.selectedUser.first_name + ' ' + this.selectedUser.last_name
            });
            this.newRoom = false;
        },
        getRooms: function(){
            let roomUsers = [];
            this.userMessages.map(x => {
                if(x.USERID != opentext.userId){
                    roomUsers.push({id: x.USERID, name: x.USERFULLNAME});
                }
                if(x.RECIPIENTID != opentext.userId){
                    roomUsers.push({id: x.RECIPIENTID, name: x.RECIPIENTFULLNAME});
                }
            });
            roomUsers = [...new Map(roomUsers.map(item => [item["id"], item])).values()];
            let iter = 1;
            const rooms = [];
            for (const user of roomUsers) {
                rooms.push({
                    roomId: ++iter,
                    roomName: user.name,
                    users: [
                        {
                          _id: opentext.userId,
                          username: opentext.firstName + ' ' + opentext.lastName,
                        },
                        {
                            _id: user.id,
                            username: user.name
                        }
                    ]
                })
            }
            this.rooms = rooms;
        },
        addNewRoom: function(user){
            let iter = this.rooms[this.rooms.length - 1].roomId;
            this.rooms.push({
                roomId: ++iter,
                roomName: user.name,
                users: [
                    {
                      _id: opentext.userId,
                      username: opentext.firstName + ' ' + opentext.lastName,
                    },
                    {
                        _id: user.id,
                        username: user.name
                    }
                ]
            })
        }
    }
}
</script>

<style scoped>
    .row + .row {
        margin-top: 0px;
    }
    .vac-card-window .vac-chat-container{
        width: 85vw !important;
    }
</style>