<template>
    <div class="d-flex align-center">
      <div class="mr-3">
          <v-textarea
           label="Question"
           v-model="pollQuestion.value.question"
           @change="changeQuestion"
           solo
           hide-details></v-textarea>
      </div>
      <div class="mr-3">
          <v-container>
              <v-row v-for="item in pollQuestion.value.choices" :key="item.id">
                <the-poll-choices :choice="item" @added="selectedChoice"></the-poll-choices>
                 <v-col class="d-flex align-center">
                     <v-btn @click="addChoice" icon><v-icon>mdi-plus-circle-outline</v-icon></v-btn>
                     <v-btn v-show="pollQuestion.value.choices.length > 1" @click="removeChoice(item.id)" icon><v-icon>mdi-minus-circle-outline</v-icon></v-btn>
                 </v-col>
              </v-row> 
          </v-container>
      </div>
      <div class="mr-3">
          <v-radio-group v-model="pollQuestion.value.type" @change="changeType" hide-details>
              <v-radio label="Radio Button" :value="1">
              </v-radio>
              <v-radio label="Checkbox" :value="2">
              </v-radio>
          </v-radio-group>
      </div>
    </div> 
</template>

<script>
import ThePollChoices from './ThePollChoices.vue';
import { v4 as uuidv4 } from 'uuid';
export default {
    name: 'ThePollQuestions',
    components: { ThePollChoices },
    props: ['pollQuestion'],
    data: () => ({
    }),
    methods: {
        emitQuestion: function(obj){
            this.$emit('question', obj);
        },
        selectedChoice: function(obj){
           const ind = this.pollQuestion.value.choices.findIndex(x=>x.id == obj.id);
           if(ind == -1){
               this.pollQuestion.value.choices.push(obj);
           }else{
               this.pollQuestion.value.choices[ind] = obj;
           }
        },
        changeType: function(){
            this.emitQuestion({
                id: this.pollQuestion.id,
                value: this.pollQuestion.value
            });
        },
        changeQuestion: function(){
            this.emitQuestion({
                id: this.pollQuestion.id,
                value: this.pollQuestion.value
            });
        },
        addChoice: function(){
            this.pollQuestion.value.choices.push({id: uuidv4(), value:""})
        },
        removeChoice: function(id){
            const ind = this.pollQuestion.value.choices.findIndex(x=>x.id == id);
            this.pollQuestion.value.choices.splice(ind, 1);
        }
    }
}
</script>

<style scoped>
    .row + .row {
        margin-top: 0px !important;
    }
</style>