<template>
    <div>
      <v-card>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Adı
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in polls"
                  :key="item.GUID"
                >
                  <td>{{ item.NAME }}</td>
                  <td>
                      <v-btn icon @click="editCurrentPoll(item.ID, item.NAME)">
                          <v-icon>mdi-pencil-outline</v-icon>
                      </v-btn>
                      <v-btn icon @click="deleteCurrentPoll(item.ID)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            persistent
            max-width="500px"
            max-height="300px"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text class="float-right" @click="dialog = true">
               <v-icon>mdi-plus</v-icon>Yeni Sorğular
              </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <v-container>
                <v-row>
                  <v-col></v-col>
                  <v-col class="d-flex justify-center"><span class="dialog-name">Yeni Sorğular</span></v-col>
                  <v-col>
                    <v-btn 
                      icon
                      class="float-right"
                      @click="close"
                     >
                     <v-icon>mdi-close</v-icon>
                    </v-btn> 
                  </v-col>
                </v-row>
              </v-container>           
             </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                    <v-col>
                        <v-text-field
                            label="Adı"
                            v-model="pollName"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-textarea
                            label="Təsnifat"
                            v-model="description"
                        ></v-textarea>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn text @click="createNewPoll">Yarat</v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          </v-dialog>
          <v-dialog
            v-model="editDialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
          >
            <v-card>
              <v-toolbar
                dark
                color="primary"
              >
                <v-btn
                  icon
                  dark
                  @click="closeEdit();"
                >
                  <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title>Səsvermə</v-toolbar-title>
              </v-toolbar>
              <v-divider></v-divider>
              <v-card-text>
                  <v-container>
                    <v-row>
                          <v-col cols="6">
                            <v-textarea
                                label="Instruction"
                                solo
                                v-model="payload.instruction"
                            >
                            </v-textarea>
                          </v-col>
                    </v-row> 
                    <v-row v-for="item in questions" :key="item.id">
                        <v-col class="d-flex align-center">
                            <the-poll-questions :pollQuestion="item" @question="createdQuestion"></the-poll-questions>    
                            <div class="d-flex align-center">
                                <v-btn @click="addQuestion" icon><v-icon>mdi-plus-circle-outline</v-icon></v-btn>
                                <v-btn v-show="questions.length > 1" @click="removeQuestion(item.id)" icon><v-icon>mdi-minus-circle-outline</v-icon></v-btn>
                            </div>
                        </v-col>
                    </v-row>  
                    <v-row>
                        <v-col>
                            <v-checkbox label="Allow viewing the results before voting" v-model="payload.viewResults"></v-checkbox>
                        </v-col>
                        <v-col>
                             <v-checkbox label="Allow anonymous comments" v-model="payload.viewComments"></v-checkbox>
                        </v-col>
                    </v-row>
                  </v-container>
              </v-card-text>
              <v-card-actions class="justify-center">
                  <v-btn @click="editQuestions" text>Əlavə et</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog
            v-model="loadDialog"
            hide-overlay
            persistent
            width="300"
          >
            <v-card
              color="primary"
              dark
            >
              <v-card-text>
                Please stand by
                <v-progress-linear
                  indeterminate
                  color="white"
                  class="mb-0"
                ></v-progress-linear>
              </v-card-text>
            </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import ThePollQuestions from './ThePollQuestions.vue';
import { v4 as uuidv4 } from 'uuid';
export default {
    name: 'ThePoll',
    components: { ThePollQuestions },
    data: () => ({
      valid: true,
      dialog: false,
      loadDialog: false,
      editDialog: false,
      pollName: '',
      description: '', 
      questions: [{
          id: uuidv4(),
          value: {
                choices: [{id: uuidv4(), value:""}],
                type: '',
                question: ''
          }
      }],
      payload: {
        Questions: {},
        instruction: '',
        viewComments: false,
        viewResults: false,
      }
    }),
    computed: {
        ...mapGetters('admin', [
            'polls'
        ])
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                    that.closeEdit();
                }
        	}
        });
    },
    methods: {
        ...mapActions('admin', [
            'createPoll',
            'deletePoll',
            'addQuestions'
        ]),
        createNewPoll: async function(){
            await this.createPoll({ name: this.pollName, description: this.description });
            this.close();
        },
        close: function(){
            this.pollName = '';
            this.description  = '';
            this.dialog = false;
        },
        closeEdit: function(){
            this.editDialog = false;
            this.instruction = '';
            this.viewResults = false;
            this.viewComments = false;
            this.questions = [{
                id: uuidv4(),
                value: {
                      choices: [{id: uuidv4(), value:""}],
                      type: '',
                      question: ''
                }
            }];
        },
        editCurrentPoll: function(id, name){
          this.payload.datePicker_effectiveDate = '';
          this.payload.effectiveDate = '?'
          this.payload.expireDate = '?'
          this.payload.datePicker_expireDate = '';
          this.payload.imgId = '';
          this.payload.imgPathSaved = '';
          this.payload.path = '';
          this.payload.img_alt = '';

          this.payload.id = id;
          this.payload.oldName = name;
          this.payload.name = name;
          this.editDialog = true;
        },
        deleteCurrentPoll: async function(id){
            await this.deletePoll({id: id});
        },
        createdQuestion: function(obj){
            const ind = this.questions.findIndex(x=>x.id == obj.id);
            if(ind == -1){
                this.addQuestion();
            }else{
                this.questions[ind] = obj;
            }
        },
        addQuestion: function(){
            this.questions.push({
               id: uuidv4(),
               value: {
                     choices: [{id: uuidv4(), value:""}],
                     type: '',
                     question: ''
               }
            });
        },
        removeQuestion: function(id){
            const ind = this.questions.findIndex(x=>x.id == id);
            this.questions.splice(ind, 1);
        },
        editQuestions: async function(){
            this.modifyQuestions();
            this.closeEdit();  
            await this.addQuestions(this.payload);
            console.log(this.payload);
        },
        modifyQuestions: function(){
            for (let i = 0; i < this.questions.length; i++) {
                this.payload.Questions[`Question_${i+1}`] = this.questions[i].value.question;
                this.payload.Questions[`PollType_${i+1}`] = this.questions[i].value.type;
                for (let j = 0; j < this.questions[i].value.choices.length; j++) {
                    this.payload.Questions[`Choice_${i+1}_${j+1}`] = this.questions[i].value.choices[j].value;
                }
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .dialog-name{
        font-size: 0.8em;
    }
    .row + .row {
        margin-top: 0px !important;
    }
</style>