<template>
    <div>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Tədbirin adı
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in events"
                  :key="item.ID"
                >
                  <td>{{ item.TITLE }}</td>
                  <td>
                      <v-btn @click="changeCurrentEvent(item)" icon>
                          <v-icon>mdi-pencil-outline</v-icon>
                      </v-btn>
                      <v-btn @click="updateCurrentEvent('delete', item)" icon>
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            persistent
            max-width="500px"
            max-height="400px"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text class="float-right" @click="newEvent">
               <v-icon>mdi-plus</v-icon>
                  <span>Yeni tədbir</span>
              </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <v-container>
                <v-row>
                  <v-col></v-col>
                  <v-col class="d-flex justify-center">
                    <span v-if="! isUpdateEvent" class="dialog-name">Yeni tədbir</span>
                    <span v-if="isUpdateEvent" class="dialog-name">Tədbir</span>
                  </v-col>
                  <v-col>
                    <v-btn 
                      icon
                      class="float-right"
                      @click="close"
                     >
                     <v-icon>mdi-close</v-icon>
                    </v-btn> 
                  </v-col>
                </v-row>
              </v-container>           
             </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="6">
                      <v-text-field
                        label="Adı"
                        required  
                        v-model="eventName"
                        hide-details="true"
                        solo
                      ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-checkbox
                            label="All Time Event"
                            hide-details="true"
                            v-model="allTimeEvent"
                            @change="allTimeEventChanged"
                        >
                        </v-checkbox>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-menu
                          ref="menu1"
                          v-model="menu1"
                          :close-on-content-click="false"
                          transition="scale-transition"
                          max-width="290px"
                          min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="dateFormatted"
                              label="Start Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu1 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date"
                            no-title
                            @input="closeDatePicker"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep1"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu1')"
                          ></v-time-picker>
                        </v-menu>
                    </v-col>
                    <v-col>
                        <v-menu
                            ref="menu2"
                            v-model="menu2"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            max-width="290px"
                            min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="date2Formatted"
                              label="End Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu2 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date2"
                            no-title
                            @input="closeDatePicker"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep2"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu2')"
                          ></v-time-picker>
                        </v-menu>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Yeri"
                      required  
                      v-model="location"
                      hide-details="true"
                      v-if="isUpdateEvent"
                      solo
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-textarea
                      label="Təsvir"
                      required  
                      v-model="description"
                      hide-details="true"
                      v-if="isUpdateEvent"
                      rows="3"
                      solo
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn v-if="!isUpdateEvent" text @click="createNewEvent">Yarat</v-btn>
                    <v-btn v-if="isUpdateEvent" text @click="updateCurrentEvent('update')">Yenilə</v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import moment from 'moment';
export default {
    name: 'TheEvents',
    data: vm => ({
      valid: true,
      dialog: false,
      eventName: '',
      location: '',
      description: '',
      dialogError: false,
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      dateFormatted: moment().format('DD/MM/YYYY, hh:mm'),
      date2Formatted: moment().format('DD/MM/YYYY, hh:mm'),
      menu1: false,
      menu2: false,
      isDate: true,
      timeStep1: '00:00',
      timeStep2: '00:00',
      allTimeEvent: false,
      isUpdateEvent: false,
      id: ''
    }),
    computed: {
        ...mapGetters('calendar', [
            'events'
        ]),
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions('calendar', [
          'addEvent',
          'updateEvent'
        ]),
        createNewEvent: async function(){
          let newEvent = {
            title: '',
            allDayEvent: false,
            startDate: '',
            endDate: ''
          }
          newEvent.title = this.eventName;
          newEvent.allDayEvent = this.allTimeEvent;

          let hour1 = this.timeStep1.split(':')[0];
          let hour2 = this.timeStep2.split(':')[0];
          let ampm1 = 'AM';
          let ampm2 = 'AM';
          if(hour1 > 12) ampm1 = 'PM'
          if(hour2 > 12) ampm2 = 'PM'
          newEvent.startDate = moment(this.date).format('MM/DD/YYYY') + ' ' + this.timeStep1 + ' ' + ampm1;
          newEvent.endDate = moment(this.date2).format('MM/DD/YYYY') + ' ' + this.timeStep2 + ' ' + ampm2;
          
          this.addEvent(newEvent);
          this.close();
        },
        deleteCurrentInstruction: async function(id){},
        editCurrentInstruction: async function(id){},
        close: function(){
            this.eventName = '';
            this.dialog = false;
            this.location = '';
            this.description = '';
            this.isUpdateEvent = false;
            this.id = '';
        },
        closeDatePicker(){
            this.isDate = false;
        },
        closeTimePicker(type){
            this.isDate = true;
            if(type == 'menu1'){
                this.dateFormatted = moment(this.date).format('DD/MM/YYYY') + ', ' + this.timeStep1;
                this.menu1 = false;
            }else{
                this.date2Formatted = moment(this.date2).format('DD/MM/YYYY') + ', ' + this.timeStep2;
                this.menu2 = false;
            }
        },
        allTimeEventChanged(){

        },
        changeCurrentEvent: function(event){
          this.eventName = event.TITLE;
          this.location = JSON.parse(event.EXTENDEDDATA).location;
          this.description = event.DETAILS != '?' ? event.DETAILS : '' ;
          this.isUpdateEvent = true;
          this.dialog = true;
          this.id = event.ID;
        },
        newEvent: function(){
          this.isUpdateEvent = false;
          this.dialog = true;
        },
        updateCurrentEvent: async function(status, obj){
           let updateEvt = {
            id: '',
            title: '',
            allDayEvent: false,
            startDate: '',
            endDate: '',
            location: '',
            details: ''
          }
          updateEvt.id = this.id;
          updateEvt.title = this.eventName;
          updateEvt.allDayEvent = this.allTimeEvent;
          updateEvt.location = this.location;
          updateEvt.details = this.description;

          let hour1 = this.timeStep1.split(':')[0];
          let hour2 = this.timeStep2.split(':')[0];
          let ampm1 = 'AM';
          let ampm2 = 'AM';
          if(hour1 > 12) ampm1 = 'PM'
          if(hour2 > 12) ampm2 = 'PM'
          updateEvt.startDate = moment(this.date).format('MM/DD/YYYY') + ' ' + this.timeStep1 + ' ' + ampm1;
          updateEvt.endDate = moment(this.date2).format('MM/DD/YYYY') + ' ' + this.timeStep2 + ' ' + ampm2;
          if(status == 'delete'){
            updateEvt.isDelete = true;
          }else{
            updateEvt.isDelete = false;
          }
          if(obj){
            updateEvt.id = obj.ID;
            updateEvt.title = obj.TITLE;
          }
          await this.updateEvent(updateEvt);
          this.close();
        }
    }
}
</script>

<style lang="scss" scoped>
  .dialog-name{
    font-size: 0.8em;
  }
  .v-error{
    color: red;
  }
  .row + .row {
    margin-top: 0px !important;
  }
</style>