<template>
      <v-sheet
        rounded="lg"
        height="85vh"
      >
        <v-container>
          <v-row>
            <v-col cols="2">
              <v-img max-height="80" max-width="80" :src="photo"></v-img>            
            </v-col>
            <v-col cols="5">
              <v-file-input @change="imageAdded" accept="image/*" class="mt-4" outlined></v-file-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field label="UserName" :value="userName" outlined hide-details="true"></v-text-field>
            </v-col>
            <v-col>
               <v-text-field label="E-Mail" :value="email" outlined hide-details="true"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field label="Ad" v-model="firstName" outlined hide-details="true"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field label="Soyad" :value="user.lastName" outlined hide-details="true"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field label="Ata adı" :value="user.middleName" outlined hide-details="true"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select label="Cinsi" :items="genders" :value="gender" outlined hide-details="true"></v-select>
            </v-col>
            <v-col>
              <v-dialog
                ref="dialog"
                v-model="birthdayModal"
                :return-value.sync="date"
                persistent
                width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date"
                    label="Ad günü"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    outlined
                    hide-details="true"
                  ></v-text-field>
                </template>
                <v-date-picker
                v-model="date"
                scrollable
              >
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="primary"
                  @click="birthdayModal = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.dialog.save(date)"
                >
                  OK
                </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-col>
            <v-col>
              <v-text-field label="Idarə" :value="user.officeLocation" outlined hide-details="true"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field label="Daxili" :value="user.internalPhone" outlined hide-details="true"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field label="Mobil" :value="user.cellPhone" outlined hide-details="true"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field label="Vəzifə" :value="user.title" outlined hide-details="true"></v-text-field>
            </v-col>
          </v-row>
          <v-row> 
            <v-col cols="2">
              <v-btn text @click="save">Yadda saxla</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-sheet>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 import moment from 'moment';
 export default {
     computed: {
     ...mapGetters('profile', [
       'user'
     ])
   },
   mounted: function(){
     if(this.user.birthData)
      this.date = moment(new Date(this.user.birthData)).format('DDDD-MM-DD');
     if(this.user.gender){
        if(this.user.gender === 'Male')
          this.gender = 'Kişi';
        else
          this.gender = 'Qadın'
     }
     this.photo = this.user.photo;
     this.userName = this.user.userName;
     this.email = this.user.email;
     this.firstName = this.user.firstName;
   },
   watch: {
     'user.birthData': function(val){
       this.date = moment(new Date(val)).format('YYYY-MM-DD');
     },
     'user.gender': function(val){
       if(val === 'Male')
        this.gender = 'Kişi';
       else
        this.gender = 'Qadın'
     },
     'user.photo': function(val){
       this.photo = val;
     },
     'user.userName': function(val){
       this.userName = val;
     },
     'user.email': function(val){
       this.email = val;
     },
     'user.firstName': function(val){
       this.firstName = val;
     }
   },
   data: ()=> ({
      genders: ['', 'Kişi', 'Qadın'],
      gender: '',
      date: new Date().toISOString().substr(0, 10),
      birthdayModal: false,
      photo: '',
      userName: '',
      email: '',
      firstName: ''
   }),
   methods: {
     ...mapActions('profile', [
       'addGeneralData'
     ]),
     imageAdded: async function(val){
       this.photo = URL.createObjectURL(val);
     },
     save: function(){
       this.addGeneralData({
         userId: this.user.userId,
         userName: this.userName,
         firstName: this.firstName
       });
     }
   }
 }
</script>

<style lang="scss" scoped>
    // .row + .row {
    //     margin-top: 0px !important;
    // }
</style>
