<template>
    <v-col>
        <v-text-field v-model="choice.value" @change="changeChoice" solo hide-details></v-text-field>
    </v-col>
</template>

<script>
export default {
    name: 'ThePollChoices',
    props: ['choice'],
    data: () => ({
    }),
    methods: {
        changeChoice: function(val){
            this.$emit('added', { id: this.choice.id, value: this.choice.value });
        }
    }
}
</script>

<style scoped>
    .row + .row {
        margin-top: 0px !important;
    }
</style>