<template>
    <div>
        <TEXTAREA CLASS="valueEditable ckeditor" :NAME="name" :ID="id" :ROWS="rows" :COLS="cols" WRAP=SOFT></TEXTAREA>
    </div>
</template>

<script>
export default {
    name: 'TheRichTextEditor',
    props: [ 'id', 'name', 'rows', 'cols'],
    mounted: function() {    
        if(document.getElementsByTagName('textarea').length > 0){
           this.activateCkEditor();
        }
    },
    methods: {
        activateCkEditor(){
            CKEDITOR.replace( this.id ,
            {
                customConfig: opentext.ckEditorConfigFile,
                language: 'en',
                toolbar: 'toolbarLightSource',
            on:
                {
                    'instanceReady' : function( evt ){
                        var blockTags = ['div','h1','h2','h3','h4','h5','h6','p','pre','li','blockquote','ul','ol', 'table','thead','tbody','tfoot','td','th'];

                        for (var i = 0; i < blockTags.length; i++)
                        {
                                evt.editor.dataProcessor.writer.setRules( blockTags[i], {
                                    indent : false,
                                    breakBeforeOpen : true,
                                    breakAfterOpen : false,
                                    breakBeforeClose : false,
                                    breakAfterClose : false
                        }   );
                        }
                    }
                }
            });
        }
    }
}
</script>

