<template>
      <v-sheet
        rounded="lg"
        min-height="85vh"
      >
        <v-container>
           <v-row>
             <v-col>
               <v-tabs
                 background-color="primary"
                 dark
               >
                 <v-tab v-for="content in contents" :key="content" @click="getContent(content)">
                   {{content}}
                 </v-tab>
               </v-tabs>
             </v-col>
           </v-row>
            <v-row v-if="num == 1">
               <v-col>
                  <the-admin-instruction></the-admin-instruction>
               </v-col>
            </v-row>
            <v-row v-if="num == 2">
              <v-col>
                <the-admin-video-instruction></the-admin-video-instruction>
              </v-col>
            </v-row>
            <v-row v-if="num == 3">
              <v-col>
                <the-events></the-events>
              </v-col>
            </v-row>
            <v-row v-if="num == 4">
              <v-col>
                <the-poll></the-poll>
              </v-col>
            </v-row>
            <v-row v-if="num == 5">
              <v-col>
                <the-general-news></the-general-news>
              </v-col>
            </v-row>
        </v-container>
      </v-sheet>
</template>

<script>
import { mapActions } from 'vuex';
import TheAdminInstruction from '../components/TheAdminInstruction.vue';
import TheAdminVideoInstruction from '../components/TheAdminVideoInstruction.vue';
import TheEvents from '../components/TheEvents.vue';
import TheGeneralNews from '../components/TheGeneralNews.vue';
import ThePoll from '../components/ThePoll.vue';
 export default {
   components: { TheAdminInstruction, TheAdminVideoInstruction, TheEvents, ThePoll, TheGeneralNews },
   data: () => ({
    tab: null,
    num: 1,
    contents: ['Təlimatlar', 'Video Təlimatlar', 'Tədbirlər', 'Sorğular', 'Yeniliklər']
  }),
   created: async function() {
     this.getApiData({
        prefix: 'admin',
        name: 'polls',
        webreportId: opentext.webreportIds.childrenElementsId,
        params: `&parentid=${opentext.cicCommunityPoll}`
     });
   },
   methods: {
     ...mapActions([
       'getApiData'
     ]),
     getContent: function(content){
       switch (content) {
         case 'Təlimatlar':
           this.num = 1;
           break;
         case 'Video Təlimatlar':
           this.num = 2;
           break;
         case 'Tədbirlər':
           this.num = 3;
           break;
         case 'Sorğular':
           this.num = 4;
           break;
          case 'Yeniliklər':
            this.num = 5;
            break;
         default:
           break;
       }
     }
   }
 }
</script>

<style lang="scss" scoped>
 
</style>
