<script>
import { mapGetters, mapActions } from 'vuex';
import Status from '../components/Status.vue';
import TheCalendar from '../components/TheCalendar.vue';
import TheBlog from '../components/TheBlog.vue';
export default {
  name: 'Dashboard',
  components: { TheCalendar, Status, TheBlog },
  data: () => ({
    dialog: false,
    colors: [
          'primary',
          'secondary',
          'yellow darken-2',
          'red',
          'orange',
    ],
    tasks: [
          {
            title: 'Job 1',
            toWhom: 'Vugar',
          },
          {
            title: 'Job 2',
            toWhom: 'Seba',
    },],
    vacancyDialog: false,
    vacancyTitle: '',
    vacancyDialogData: '',
    motivationHistoryDialog: false,
    motivationHistoryTitle: '',
    motivationHistoryDialogData: '',
    pollResult: [],
    currentIndex: 0,
    isPollSubmit: true,
    pollCurrentAnswer: '',
    pollCurrentQuestion: '',
    pollAnswers: [],
    isBlog: false
  }),
  computed: {
    ...mapGetters(['isLoaded']),
    ...mapGetters('hr', [
      'vacancies'
    ]),
    ...mapGetters('admin', [
      'activePollData',
      'myVoteInActivePoll',
      'motivationHistories'
    ]),
    computedVacancies: function(){
      return this.vacancies.map((x) => { return {id: x.GUID, data: JSON.parse(x.EXTENDEDDATA)} });
    },
    computedMotivationHistories: function(){
      return this.motivationHistories.map((x) => {return {id: x.DATAID, headline: x.NAME, date: x.MODIFYDATE, content: JSON.parse(x.DATA).blogContent }});
    },
    question: function(){
      if(this.pollResult.length > 0){
        return this.pollResult[this.currentIndex].Question;
      }
    },
    choices: function(){
      if(this.pollResult.length > 0)
        return this.pollResult[this.currentIndex].Choices
    }
  },
  methods: {
    ...mapActions('admin', [
      'convertAssocToJSON',
      'addPollAnswer',
      'getPollResult',
      'createMotivationHistory',
      'getMotivationHistories'
    ]),
    showVacancyDialog: function(data){
      this.vacancyDialog = true;
      this.vacancyTitle = data.Headline;
      this.vacancyDialogData = data.Story;
    },
    showMotivationHistoryDialog: function(data){
       this.motivationHistoryDialog = true;
       this.motivationHistoryTitle = data.headline;
       this.motivationHistoryDialogData = data.content;
    },
    nextQuestion: function(){
      if(this.pollResult.length > this.currentIndex + 1){
        this.currentIndex++;
        this.$nextTick(() => {
          this.setCurrentQuestion();
          this.setCurrentAnswers();
        })
      }  
    },
    previousQuestion: function(){
      if(this.currentIndex - 1 >= 0){
        this.currentIndex--;
        this.$nextTick(() => {
           this.setCurrentQuestion();
           this.setCurrentAnswers();
        })
      }
    },
    setCurrentQuestion: function(){
      this.pollCurrentQuestion = this.pollResult[this.currentIndex].Question;
    },
    setCurrentAnswers: function(){
       let pollIndex = this.pollAnswers.findIndex(x=>x.key == this.pollCurrentQuestion);
       if(pollIndex != -1){
         this.pollCurrentAnswer = this.pollAnswers[pollIndex].value;
       }else{
         this.setPollAnswer(this.pollCurrentQuestion, this.pollCurrentAnswer);
       }
    },
    selectPollAnswer: function(){
      let pollIndex = this.pollAnswers.findIndex(x=>x.key == this.pollCurrentQuestion); 
      if(pollIndex != -1){
          this.pollAnswers[pollIndex].value = this.pollCurrentAnswer;
          this.checkIfAllAnswersIsSet();
      }
    },
    setPollAnswer: function(question, answer){
      this.pollAnswers.push({key: question, value: answer});
    },
    checkIfAllAnswersIsSet: function(){
      let isAllAnsIsSet = this.pollAnswers.find(x=>x.value == undefined);
      if(!isAllAnsIsSet && (this.pollResult.length == this.pollAnswers.length)){
        this.isPollSubmit = false;
      }else{
        this.isPollSubmit = true;
      }
    },
    submit: async function(){
      await this.getPollResult(this.activePollData[0].DATAID);
      await this.addPollAnswer(this.pollAnswers);
    
      console.log(this.pollAnswers);
    },
    addBlog: function(){
        this.isBlog = true;
    },
    closeBlog: function(newBlog){
        this.isBlog = false;
    },
    createBlog: async function(newBlog){
      await this.createMotivationHistory({name: newBlog.name, content: newBlog.content});
      await this.getMotivationHistories();
    }
  },
  watch: {
    activePollData: async function(val){
      var data = JSON.parse(val[0].EXTENDEDDATA.replaceAll("\\u0027", "'"));
      var result = await this.convertAssocToJSON(data.Questions);
      this.pollResult = result;
      this.setCurrentQuestion();
      this.setPollAnswer(this.pollCurrentQuestion, undefined);
    }
  }
}
</script>

