<template>
    <div>
      <v-card>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Video Təlimatın adı
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in videoInstructions"
                  :key="item.GUID"
                >
                  <td>{{ item.NAME }}</td>
                  <td>
                      <v-btn icon @click="deleteCurrentInstruction(item.ID)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            persistent
            max-width="400px"
            max-height="300px"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text class="float-right" @click="dialog = true">
               <v-icon>mdi-plus</v-icon>Yeni təlimat
              </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <v-container>
                <v-row>
                  <v-col></v-col>
                  <v-col class="d-flex justify-center"><span class="dialog-name">Yeni təlimat</span></v-col>
                  <v-col>
                    <v-btn 
                      icon
                      class="float-right"
                      @click="close"
                     >
                     <v-icon>mdi-close</v-icon>
                    </v-btn> 
                  </v-col>
                </v-row>
              </v-container>           
             </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col>
                      <v-text-field
                        label="Name"
                        required  
                        v-model="instructionName"
                      ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <the-file-uploader @getFile="getFile" :accept-types="videoAccept"></the-file-uploader>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn text @click="createNewInstruction">Yarat</v-btn>
                    <span v-if="showValidation" class="v-error">*Bu adla təlimat mövcuddur</span>
                  </v-col>
                  <v-col v-if="showValidation"></v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          </v-dialog>
          <v-dialog
            v-model="loadDialog"
            hide-overlay
            persistent
            width="300"
          >
            <v-card
              color="primary"
              dark
            >
              <v-card-text>
                Please stand by
                <v-progress-linear
                  indeterminate
                  color="white"
                  class="mb-0"
                ></v-progress-linear>
              </v-card-text>
            </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import TheFileUploader from './TheFileUploader.vue';
export default {
    name: 'Instruction',
    components: { TheFileUploader },
    data: () => ({
      valid: true,
      dialog: false,
      loadDialog: false,
      instructionName: '',
      showValidation: false,
      file: null,
      videoAccept: 'video/mp4,video/x-m4v,video/*'
    }),
    computed: {
      ...mapGetters('admin', [
          'videoInstructions'
      ]),
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions('admin', [
            'createVideoInstruction',
            'deleteInstruction'
        ]),
        createNewInstruction: async function(){
          this.dialog = false;
          this.loadDialog = true;
          const response = await this.createVideoInstruction({ name: this.instructionName, file: this.file }); 
          if(response == 'Error'){
            this.showValidation = true;
            this.dialog = true;
            this.loadDialog = false;
            return;
          }
          this.loadDialog = false;
          this.close();
        },
        close: function(){
            this.instructionName = '';
            this.showValidation = false;
            this.dialog = false;
        },
        getFile: function(value){
          this.file = value;
        },
        deleteCurrentInstruction: async function(id){
          const errorResponse = await this.deleteInstruction({type: 'Video-Instruction', id: id});
          if(errorResponse){
            console.log(errorResponse);
          }
        }
    }
}
</script>

<style lang="scss" scoped>
    .dialog-name{
        font-size: 0.8em;
    }
    .v-error{
        color: red;
    }
</style>