<template>
   <v-row>
        <v-col cols="2"></v-col>
        <v-col>
          <v-card class="w-secondary">
              <v-card-title>
                  <v-container fluid>
                      <v-row>
                          <v-col>
                              <span class="fullName">{{firstName}} {{lastName}}</span>  
                              <!-- <span class="createdAt">{{createdAt}}</span>                    -->
                          </v-col>
                          <v-col>
                              <v-menu
                                v-if="isEditScreen"
                                absolute
                                offset-y
                                nudge-left = 180
                                nudge-bottom = 18
                              >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-btn 
                                    icon 
                                    class="float-right"
                                    v-bind="attrs"
                                    v-on="on"
                                  >
                                    <v-icon>mdi-dots-horizontal</v-icon>
                                  </v-btn>
                                </template>
                                <v-list>
                                    <v-list-item link>
                                        <v-list-item-icon>
                                            <v-icon>mdi-border-color</v-icon>
                                        </v-list-item-icon>
                                        <v-list-item-content>
                                            <v-list-item-title>Edit Post</v-list-item-title>
                                        </v-list-item-content>
                                    </v-list-item>
                                     <v-list-item link @click="deletePost">
                                         <v-list-item-icon>
                                            <v-icon>mdi-trash-can-outline</v-icon>
                                         </v-list-item-icon>
                                        <v-list-item-content>
                                            <v-list-item-title>Delete Post</v-list-item-title>
                                        </v-list-item-content>
                                    </v-list-item>
                                </v-list>
                              </v-menu>
                          </v-col>
                      </v-row>
                  </v-container>
              </v-card-title>
              <v-card-text class="margin-fix text-post">
                  <v-container fluid>
                       <v-row>
                          <v-col>
                              <span class="text">{{text}}</span>
                          </v-col>
                      </v-row>
                      <v-row>
                          <v-col>
                              <span v-html="attachment"></span>
                          </v-col>
                      </v-row>
                  </v-container>
              </v-card-text>
              <v-card-actions>
                    <v-container fluid>
                        <v-row>
                          <v-col>
                            <div v-if="isLike || likeNumber != 0">
                            <v-icon>mdi-thumb-up-outline</v-icon>
                            <v-tooltip bottom v-if="likeNumber != 0">
                                <template v-slot:activator="{ on, attrs }">
                                    
                                    <span v-bind="attrs" v-on="on" class="like-number ml-2">
                                        <span v-if="isLike">You and</span>
                                        <span>
                                        {{likeNumber}}
                                        <span v-if="isLike && likeNumber > 1">others</span>
                                        <span v-if="isLike && likeNumber == 1">other</span>
                                        </span>
                                    </span> 
                                </template>
                                <div v-for="person in likes" :key="person.USERID">
                                    {{person.FIRSTNAME}} {{person.LASTNAME}}
                                </div>
                            </v-tooltip>
                            <span class="like-number" v-if="isLike && likeNumber == 0">
                                {{myLike.FIRSTNAME}} {{myLike.LASTNAME}}
                            </span>
                            </div>
                          </v-col>
                        </v-row>
                        <v-divider></v-divider>
                        <v-row>
                            <v-col>
                                <v-btn block text @click="like"><v-icon :class="{like: isLike}">mdi-thumb-up-outline</v-icon><span :class="{like: isLike, 'ml-2': true}">Like</span></v-btn>
                            </v-col>
                            <v-col>
                               <v-btn block text @click="focusOnComment"><v-icon>mdi-message-outline</v-icon><span class="ml-2">Comment</span></v-btn>
                            </v-col>
                        </v-row>
                        <v-divider></v-divider>

                        <v-row class="mt-2" v-if="postData.comments.length > 1 && !showAllComments">
                            <v-col>
                                <span>
                                    <a @click="viewAllComments" class="view-comments">View all comments</a>
                                </span>
                            </v-col>
                        </v-row>

                        <div class="mt-2" v-if="postData.comments.length >= 1 && !showAllComments">
                            <the-comment :postId="postData.postId" :commentObj="postData.comments[0]" :isCommentLiked="false" :goRight="''"></the-comment>
                        </div>

                        <div class="mt-2" v-if="showAllComments">
                            <the-comment v-for="item in postData.comments" :key="item.POSTID" :postId="postData.postId" :commentObj="item" :isCommentLiked="false" :goRight="''"></the-comment>
                        </div>
                         
                        <v-row>
                            <v-text-field
                                    v-model="comment"
                                    ref="commentInput"
                                    placeholder="Write a comment..."
                                    filled
                                    dense
                                    rounded
                                    hide-details
                                    @keyup.enter="onCommentEnter"
                                    class="mt-4"
                            ></v-text-field>
                        </v-row>
                    </v-container>
             </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="2"></v-col>    
    </v-row>   
</template>

<script>
import moment from 'moment';
import { mapActions, mapGetters, mapMutations } from 'vuex';
import TheComment from './TheComment.vue';
export default {
  components: { TheComment },
    name: 'PublicPost',
    props: ['postData'],
    created: function() {
      if(this.postData.userId == opentext.userId){
          this.isEditScreen = true;
      }  
    },
    computed: {
        ...mapGetters('profile', [
           'user'
        ]),
        firstName: function(){
            return this.postData ? this.postData.firstName: '';
        },
        lastName: function(){
            return this.postData ? this.postData.lastName: '';
        },
        text: function(){
            return this.postData ? this.postData.text: '';
        },
        attachment: function(){
                return this.postData.attachment ? this.postData.attachment.Icon + '&nbsp;<a href="'+this.postData.attachment.URL+'">' 
                + this.postData.attachment.Name + '</a>&nbsp;' : '';
                //+ this.postData.attachment.Menu : ''; TODO: add it later
        },
        createdAt: function(){
            return this.postData ? moment(new Date(this.postData.createdAt)).format('DD/MM/YYYY'): '';
        },
        isLike: function(){
            return this.postData.isLike;
        },
        likeNumber: function(){
            return this.likes ? this.likes.length: 0;
        },
        likes: function(){
            return this.postData ? this.postData.likes.filter(x=>x.USERID != opentext.userId) : {}
        },
        myLike: function(){
            return this.postData ? this.postData.likes.find(x=>x.USERID == opentext.userId) : {}
        }
    },
    data: () => ({
        isEditScreen: false,
        comment: '',
        showAllComments: false
    }),
    methods: {
        ...mapActions('newsFeed', [
            'likeOrUnlikePost',
            'destroyPost',
            'createPost'
        ]),
        ...mapMutations('newsFeed', [
            'removePost'
        ]),
        like: async function(){
            let likeValue = 1;
            if(this.postData.isLike){
                likeValue = 0;
            }
            await this.likeOrUnlikePost({postId: this.postData.postId, isLike: likeValue});
            this.postData.isLike = !this.postData.isLike;
        },
        deletePost: async function() {
            await this.destroyPost(this.postData.postId);
            this.removePost(this.postData.postId);
        },
        focusOnComment: function(){
            this.$refs.commentInput.focus();
        },
        onCommentEnter: async function(){
            const id = await this.createPost({ postStatus: this.comment, inReplyToStatusId: this.postData.postId });
            this.postData.comments.unshift({FIRSTNAME: this.user.firstName, LASTNAME: this.user.lastName, post: this.comment, POSTID: id});
            this.comment = '';
        },
        viewAllComments: function(){
            this.showAllComments = true;
        }
    }
}
</script>

<style lang="scss" scoped>
    .fullName{
        font-size: 0.8em;
    }
    .text{
        color: black;
        font-size: 2em;
    }
    .margin-fix{
        margin-top: -25px !important;
    }
    .createdAt{
        display: block;
        font-size: 0.6em;
    }
    .text-post{
        overflow: auto;
        max-height: 150px;
        overflow-x: hidden;
    }
    .like{
        color: blue !important;
    }
    .view-comments{
        color: black;
    }
    .view-comments:hover{
        text-decoration: underline;
        
    }
    .like-number:hover{
        text-decoration: underline;
        cursor: pointer;
    }
    .w-secondary{
        background-color: #f7f8f8;
    }
</style>