<template>
    <v-form>
        <v-card>
            <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="4">
                            <v-text-field
                                label="Başlıq"
                                required
                                outlined
                                v-model="header"
                                hide-details="false"
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                <v-col>
                    <p><u>Əsas mətn</u></p>
                    <the-rich-text-editor 
                        :id="'story'"
                        :name="'story'"
                        :rows="5"
                        :cols="40"></the-rich-text-editor>
                </v-col>
                    </v-row>
                    <v-row>
                <v-col>
                    <v-checkbox label="Introduction" hide-details></v-checkbox> 
                     <v-radio-group row hide-details>
                      <v-radio
                        label="Publish"
                      ></v-radio>
                      <v-radio
                        label="Draft"
                      ></v-radio>
                    </v-radio-group>   
                </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="6">
                            <v-file-input
                              label="File əlavə etmək"
                              outlined
                              hide-details
                            ></v-file-input>
                        </v-col>
                        <v-col cols="6">
                            <v-file-input
                              label="Opentext-dən əlavə etmək"
                              outlined
                              hide-details
                            ></v-file-input>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col>
                            <p><u> URL Attachment</u></p>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col>
                            <v-text-field
                                label="Ad"
                                required
                                outlined
                                hide-details="false"
                            ></v-text-field>
                        </v-col>
                         <v-col>
                             <v-text-field
                                 label="URL"
                                 required
                                 outlined
                                 hide-details="false"
                                 value="http://"
                             ></v-text-field>
                         </v-col>
                    </v-row>
                </v-container>
            </v-card-text>
            <v-card-actions class="d-flex justify-center">
                <v-btn @click="createANewBlog" text>Əlavə et</v-btn>
            </v-card-actions>
        </v-card>
    </v-form> 
</template>

<script>
import TheRichTextEditor from './TheRichTextEditor.vue';
import { mapActions } from 'vuex';
export default {
  components: { TheRichTextEditor },
    name: 'TheBlog',
    data: () => ({
        header: ''
    }),
    methods: {
        clearValuesOfBlog: function(){
            this.header = '';
            CKEDITOR.instances['story'].setData('');
        },
        createANewBlog: async function(){
            let content = CKEDITOR.instances['story'].getData();
            this.$emit('createTheBlog', {name: this.header, content: content});
            this.$emit('closeTheBlog', {name: this.header, content: content});  
            this.clearValuesOfBlog();
        }
    }
}
</script>

<style lang="scss" scoped>
    .row + .row {
        margin-top: 0px;
    }
</style>