<template>
    <v-container>
        <v-row>
            <v-col>
                <v-card>
                    <v-card-text>
                        <v-simple-table 
                                fixed-header 
                                dense 
                                height="300">
                                <template v-slot:default>
                                  <thead>
                                    <tr>
                                      <th class="text-left">
                                        Postlar
                                      </th>
                                      <th class="text-left">
                                        Action
                                      </th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    <tr
                                    v-for="item in blogHeadlines" :key="item.No">
                                      <td>{{item.Headline}}</td>
                                      <td>
                                          <v-btn icon>
                                              <v-icon>mdi-pencil-outline</v-icon>
                                          </v-btn>
                                          <v-btn icon>
                                              <v-icon>mdi-trash-can-outline</v-icon>
                                          </v-btn>
                                      </td>
                                    </tr>
                                  </tbody>
                                </template>
                        </v-simple-table>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn v-if="!isBlog" text @click="addBlog()">Yeni post paylaş</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>   
        </v-row>
         <v-dialog 
            v-model="isBlog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition">
            <v-toolbar
                dark
                color="primary">
              <v-spacer></v-spacer>
              <v-btn
                 icon
                 @click="isBlog = false"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
            </v-toolbar>
            <the-blog @closeTheBlog="closeBlog" @createTheBlog="createBlog"></the-blog>
         </v-dialog>
        
    </v-container>
</template>

<script>
import TheBlog from './TheBlog.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'HrGeneral',
    components: { TheBlog },
    computed: {
      ...mapGetters('hr', [
           'blogHeadlines'
        ]),
    },
    data: ()=>({
        isBlog: false
    }),
    methods: {
        ...mapActions('hr', ['createBlog']),
        addBlog: function(){
            this.isBlog = true;
        },
        closeBlog: function(newBlog){
            this.blogHeadlines.unshift({ No: newBlog.name, Headline: newBlog.name })
            this.isBlog = false;
        }
    }
}
</script>

<style lang="scss" scoped>
</style>